import React from 'react'
import { useParams, useNavigate } from 'react-router-dom'
import { useQuery } from '@tanstack/react-query'
import Swal from 'sweetalert2'
import * as AccountService from '../services/AccountService'
import * as OrderService from '../services/OrderService'
import { useSelector, useDispatch } from 'react-redux'
import { updateUser } from '../redux/userSlide'
import Navbar from '../components/Navbar'
import Footer from '../components/Footer'
import LoadingSpinner from '../components/LoadingSpinner' //  import spinner p
import './AccountDetailsPage.css'

const AccountDetailsPage = () => {
  const dispatch = useDispatch()
  const { id } = useParams()
  const navigate = useNavigate()
  const user = useSelector((state) => state.user)

  const { data: account, isLoading } = useQuery({
    queryKey: ['account-details', id],
    queryFn: () => AccountService.getAccountDetails(id),
    enabled: !!id,
  })

  const handleBuyNow = async () => {
    if (!user?.access_token) {
      Swal.fire({
        icon: 'warning',
        title: 'Qu khch vui lng ng nhp!',
        confirmButtonText: 'ng nhp',
        allowOutsideClick: false,
      }).then(() => {
        navigate('/login')
      })
      return
    }

    const result = await Swal.fire({
      title: 'Xc nhn mua',
      text: `Bn chc chn mun mua acc ny vi gi ${account.price.toLocaleString()}?`,
      icon: 'question',
      showCancelButton: true,
      confirmButtonText: 'MUA NGAY',
    })

    if (result.isConfirmed) {
      try {
        const res = await OrderService.createOrder({
          accountId: account._id,
          token: user.access_token,
        })
        Swal.fire('Thnh cng!', 'Ti khon  c mua.', 'success')
        dispatch(updateUser({ ...user, balance: user.balance - account.price }))
        navigate(`/order-details/${res.order._id}`)
      } catch (err) {
        Swal.fire('Li', err?.response?.data?.message || 'Li khi mua ti khon', 'error')
      }
    }
  }

  if (isLoading) return <LoadingSpinner /> //  Loading p
  if (!account) return <div>Khng tm thy ti khon</div>

  const displayValue = (value) => {
    return value === 0 || value === '' ? 'B mt' : value
  }

  return (
    <div>
      <Navbar />
      <div className="account-details-container">
        <h1 className="account-title">THNG TIN TI KHON</h1>
        <p className="account-id">#{account._id}</p>

        <div className="account-info">
          <div className="account-info-item">Nick <span>{displayValue(account.name)}</span></div>
          <div className="account-info-item">Rank <span>{displayValue(account.rank)}</span></div>
          <div className="account-info-item">Tng <span>{displayValue(account.champions)}</span></div>
          <div className="account-info-item">Trang phc <span>{displayValue(account.skins)}</span></div>
          <div className="account-info-item">Ngc <span>{displayValue(account.gems)}</span></div>
        </div>

        <div className="account-price">GI: {account.price.toLocaleString()} </div>
        <p className="account-contact">Mi thc mc vui lng lin h Zalo: <strong>0946 869 304</strong></p>

        <div className="account-buttons">
          <button className="buy-now" onClick={handleBuyNow}>MUA NGAY</button>
        </div>

        <div className="account-image-section">
          <h3>Hnh nh chi tit</h3>
          {account.image?.url && (
            <img src={account.image.url} alt="Main" className="account-image" />
          )}
          {account.images?.map((img, index) => (
            <img key={index} src={img.url} alt={`Detail ${index}`} className="account-image" />
          ))}
        </div>
      </div>
      <Footer />
    </div>
  )
}

export default AccountDetailsPage
