import React from 'react';
import './ProductCard.css';
import { useNavigate } from 'react-router-dom';

function ProductCard({ account }) {
  const navigate = useNavigate();
  const imgUrl = account.image?.url || '/default.jpg';

  const handleViewDetails = () => {
    navigate(`/accountDetails/${account._id}`);
  };

  // Hm kim tra: nu = 0 hoc = '' th tr "B mt"
  const displayValue = (value) => {
    return value === 0 || value === '' ? 'B mt' : value;
  };

  return (
    <>
    <div className="product-card">
      <img src={imgUrl} alt={account.name} />
      <h3>{account.name}</h3>

      <div className="info-row">
        <span>
          S Tng: <span className="highlight-red">{displayValue(account.champions)}</span>
        </span>
        <span>
          S Skin: <span className="highlight-red">{displayValue(account.skins)}</span>
        </span>
      </div>

      <div className="info-row">
        <span>
          Ngc: <span className="highlight-red">{displayValue(account.gems)}</span>
        </span>
        <span>
          Rank: <span className="highlight-red">{displayValue(account.rank)}</span>
        </span>
      </div>

      <div className="footer">
        <span className="price-box">{account.price.toLocaleString()}</span>
        <button onClick={handleViewDetails}>CHI TIT</button>
      </div>
    </div>
    </>
  );
}

export default ProductCard;
