import React, { useState } from 'react'
import Header from '../components/Header'
import Footer from '../components/Footer'
import './SignUpPage.css'
import Swal from 'sweetalert2'
import { useNavigate } from 'react-router-dom'
import { registerUser } from '../services/UserService'
import Navbar from '../components/Navbar'

const SignUpPage = () => {
  const [username, setUsername] = useState('')
  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')
  const [confirmPassword, setConfirmPassword] = useState('')
  const [isLoading, setIsLoading] = useState(false) //  Thm loading
  const navigate = useNavigate()

  const handleSubmit = async (e) => {
    e.preventDefault()
    if (password !== confirmPassword) {
      Swal.fire('Li', 'Mt khu xc nhn khng khp!', 'error')
      return
    }

    try {
      setIsLoading(true) //  bt loading
      const res = await registerUser({ username, email, password, confirmPassword })
      Swal.fire('Thnh cng', 'To ti khon thnh cng!', 'success')
      navigate('/login')
    } catch (err) {
      Swal.fire('Li', err?.response?.data?.message || 'Li khi ng k', 'error')
    } finally {
      setIsLoading(false) //  tt loading
    }
  }

  return (
    <>
      {/* <Header /> */}
      <Navbar />
      <div className="signup-container">
        <h2>ng k thnh vin</h2>
        <form onSubmit={handleSubmit} className="signup-form">
          <input
            type="text"
            placeholder="Username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            required
          />
          <input
            type="email"
            placeholder="Email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder="Mt khu"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            required
          />
          <input
            type="password"
            placeholder="Xc nhn mt khu"
            value={confirmPassword}
            onChange={(e) => setConfirmPassword(e.target.value)}
            required
          />

          <button type="submit" className="btn-signup" disabled={isLoading}>
            {isLoading ? (
              <div className="spinner"></div> //  Nu loading th hin spinner
            ) : (
              'ng k'
            )}
          </button>

          <div className="signup-separator">- Bn  c ti khon -</div>
          <button type="button" className="btn-register" onClick={() => navigate('/login')}>
            ng nhp
          </button>
        </form>
      </div>
      <Footer />
    </>
  )
}

export default SignUpPage
