import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { getAllAccounts, getAllCategories } from '../services/api'
import ProductCard from '../components/ProductCard'
import Header from '../components/Header'
import Footer from '../components/Footer'
import './CategoryPage.css';
import Navbar from '../components/Navbar'

function CategoryPage() {
  const { id } = useParams()
  const [accounts, setAccounts] = useState([])
  const [categoryName, setCategoryName] = useState('')
  const [page, setPage] = useState(1)
  const [totalPages, setTotalPages] = useState(1)
  const [isPending, setIsPending] = useState(false)

  // B lc
  const [minPrice, setMinPrice] = useState('')
  const [maxPrice, setMaxPrice] = useState('')
  const [rank, setRank] = useState('Tt c rank')
  const [sortPrice, setSortPrice] = useState('none')
  const [isLuckyCategory, setIsLuckyCategory] = useState(false)


  const fetchAccounts = async (customPage = 1) => {
    setIsPending(true)
    try {
      const params = {
        type: id,
        page: customPage,
        limit: 50,
        minPrice,
        maxPrice,
        sortPrice: sortPrice !== 'none' ? sortPrice : '',
        rank: rank !== 'Tt c rank' ? rank : ''
      }

      const data = await getAllAccounts(params)
      setAccounts(data.accounts || [])
      setPage(data.currentPage)
      setTotalPages(data.totalPages)
    } catch (err) {
      console.error('Li ly acc:', err)
    } finally {
      setIsPending(false)
    }
  }

  const fetchCategoryName = async () => {
    try {
      const allCats = await getAllCategories()
      const found = allCats.find((c) => c._id === id)
      if (found) {
        setCategoryName(found.name)
        setIsLuckyCategory(found.name.toLowerCase().includes('th vn may'))
      }
    } catch (err) {
      console.error('Khng ly c tn category:', err)
    }
  }

  useEffect(() => {
    fetchAccounts()
    fetchCategoryName()
  }, [id])

  const handleSearch = () => {
    fetchAccounts(1)
  }

 
  return (
    <>
      {/* <Header /> */}
      <Navbar/>
      <div className="category-page-container">
        <div className="category-wrapper">
        <div className="category-header-narrow">
          <h1 className="category-title">Danh mc {categoryName}</h1>
          {isLuckyCategory && (
  <div className="lucky-description">
    {categoryName.toLowerCase().includes('20k') && (
      <>
        <p><strong>TH VN MAY LIN QUN 20K:</strong></p>
        <p> 100% Ti Khon ng</p>
        <p> 100% Khng C SDT V Khng C Gmail</p>
        <p> 100% Acc T 10 Tng</p>
        <p> 50% Nhn Acc SIU KHNG</p>
        <p style={{ fontStyle: 'italic', color: '#e60000' }}>
          Lu  ! Th Vn May Chp Nhn HN - XUI
        </p>
      </>
    )}
    {categoryName.toLowerCase().includes('50k') && (
      <>
        <p><strong>TH VN MAY LIN QUN 50K:</strong></p>
        <p> 100% Ti Khon ng</p>
        <p> 100% Trng Thng Tin Khng C SDT V Gmail</p>
        <p> 100% Acc T 30 Tng</p>
        <p> 70% Nhn Acc SIU KHNG</p>
        <p style={{ fontStyle: 'italic', color: '#e60000' }}>
          Lu  ! Th Vn May Chp Nhn HN - XUI
        </p>
      </>
    )}
    {categoryName.toLowerCase().includes('100k') && (
      <>
        <p><strong>TH VN MAY LIN QUN 100K:</strong></p>
        <p> 100% Ti Khon ng</p>
        <p> 100% i c Thng Tin</p>
        <p> 100% Acc T 40 Tng</p>
        <p> 70% Nhn Acc SIU KHNG</p>
      </>
    )}
    {categoryName.toLowerCase().includes('200k') && (
      <>
        <p><strong>TH VN MAY LIN QUN 200K:</strong></p>
        <p> 100% Ti Khon ng</p>
        <p> 100% Trng Thng Tin</p>
        <p> 100% Acc T 50 Tng</p>
        <p> 80% Nhn Acc SIU KHNG</p>
      </>
    )}
    {categoryName.toLowerCase().includes('300k') && (
      <>
        <p><strong>TH VN MAY LIN QUN 300K:</strong></p>
        <p> 100% Ti Khon ng</p>
        <p> 100% Acc i c thng tin</p>
        <p> 100% Acc T 60 Tng</p>
        <p> 80% Nhn Acc SIU KHNG</p>
      </>
    )}
    {categoryName.toLowerCase().includes('500k') && (
      <>
        <p><strong>TH VN MAY LIN QUN 500K:</strong></p>
        <p> 100% Ti Khon ng</p>
        <p> 100% Acc T 100 Tng</p>
        <p> 100% Acc i c thng tin</p>
        <p> 80% Nhn Acc SIU KHNG</p>
      </>
    )}
  </div>
)}

          </div>
          <div className="filter-container">
            <input
              type="number"
              placeholder="Gi ti thiu"
              value={minPrice}
              onChange={(e) => setMinPrice(e.target.value)}
            />
            <input
              type="number"
              placeholder="Gi ti a"
              value={maxPrice}
              onChange={(e) => setMaxPrice(e.target.value)}
            />
            {!isLuckyCategory && (
              <select value={rank} onChange={(e) => setRank(e.target.value)}>
                <option>Tt c rank</option>
                <option>Bc</option>
                <option>Vng</option>
                <option>Bch Kim</option>
                <option>Kim Cng</option>
                <option>Tinh Anh</option>
                <option>Cao Th</option>
                <option>Thch u</option>
              </select>
            )}
  
            <select value={sortPrice} onChange={(e) => setSortPrice(e.target.value)}>
              <option value="none">Sp xp gi</option>
              <option value="asc">Gi tng dn</option>
              <option value="desc">Gi gim dn</option>
            </select>
            <button onClick={handleSearch}>Tm</button>
          </div>
  
          {isPending ? (
            <div className="spinner" />
          ) : (
            <div className="product-list">
              {accounts.map((acc) => (
                <ProductCard key={acc._id} account={acc} />
              ))}
            </div>
          )}
  
          <div className="pagination">
            <button onClick={() => fetchAccounts(page - 1)} disabled={page === 1}>
              &laquo;
            </button>
            {Array.from({ length: totalPages }, (_, i) => (
              <button key={i + 1} className={page === i + 1 ? 'active' : ''} onClick={() => fetchAccounts(i + 1)}>
                {i + 1}
              </button>
            ))}
            <button onClick={() => fetchAccounts(page + 1)} disabled={page === totalPages}>
              &raquo;
            </button>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
  
}

export default CategoryPage
