import React, { useEffect, useState } from 'react'
import './NapATMPage.css'
import { useSelector } from 'react-redux'
import Header from '../components/Header'
import Footer from '../components/Footer'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
import Swal from 'sweetalert2'
import Navbar from '../components/Navbar'

const NapATMPage = () => {
  const user = useSelector((state) => state.user)
  console.log(" user redux:", user)

  const [paymentInfo, setPaymentInfo] = useState(null)
  const navigate = useNavigate()

  useEffect(() => {
    if (user === undefined) return; // ch Redux
  
    console.log(" user redux:", user)
  
    if (!user || typeof user._id !== 'string') {
      Swal.fire({
        title: 'Thng bo',
        text: 'Qu khch vui lng ng nhp  tip tc!',
        icon: 'warning',
        confirmButtonText: 'OK',
        allowOutsideClick: false,
        allowEscapeKey: false
      }).then(() => {
        navigate('/login')
      })
      return
    }
  
    //  Gi API nu  login
    const fetchPaymentInfo = async () => {
      try {
        const res = await axios.get(`${process.env.REACT_APP_API_URL}/payment-info`)
        const info = res.data[0]
        const transferNote = `NAP ${user._id.slice(-6)}`
        setPaymentInfo({ ...info, transferNote })
      } catch (error) {
        console.error(' Li khi ly thng tin np:', error)
      }
    }
  
    fetchPaymentInfo()
  }, [user, navigate])
  
  const handleConfirmTransfer = () => {
    Swal.fire({
      icon: 'success',
      title: 'Bn  xc nhn thnh cng!',
      showConfirmButton: false,
      timer: 2000
    })
  }
  

  const handleCopy = (text) => {
    navigator.clipboard.writeText(text)
    Swal.fire({
      toast: true,
      position: 'top-end',
      icon: 'success',
      title: ` sao chp: ${text}`,
      showConfirmButton: false,
      timer: 1500
    })
  }

  if (!paymentInfo) {
    return (
      <>
        {/* <Header /> */}
        <Navbar/>
        <div className="napatm-container">
          <h2>ang ti thng tin np...</h2>
        </div>
        <Footer />
      </>
    )
  }

  return (
    <>
      {/* <Header /> */}
      <Navbar/>
      <div className="napatm-container">
        <h1 className="napatm-title">NP ATM - V</h1>
        <span style={{fontSize:'12px'}} className="napatm-note">(Hin ti qu khch nu cn np th co vui lng lin h Zalo 0946896304, xin cm n!)</span>


        <div className="napatm-field">
          <label>S ti khon:</label>
          <div className="napatm-row">
            <input value={paymentInfo.accountNumber} disabled />
            <button className="copy-btn" onClick={() => handleCopy(paymentInfo.accountNumber)}>Copy</button>
          </div>
        </div>

        <div className="napatm-field">
          <label>Ngn Hng:</label>
          <input value={paymentInfo.bankName} disabled />
        </div>

        <div className="napatm-field">
          <label>H v tn:</label>
          <input value={paymentInfo.fullName} disabled />
        </div>
        <div className="napatm-field">
          <label>Ni dung:</label>
          <div className="napatm-row">
            <input value={paymentInfo.transferNote} disabled />
            <button className="copy-btn" onClick={() => handleCopy(paymentInfo.transferNote)}>Copy</button>
          </div>
        </div>
        <div className="napatm-qr">
          <img src={paymentInfo.qrImage?.url} alt="QR Code" />
        </div>

        <div className="napatm-note">
          Ni dung chuyn tin l <strong>{paymentInfo.transferNote}</strong>  c x l t ng
        </div>
        <div className="napatm-warning">
          Sau khi chuyn khon xong vui lng bm xc nhn  chuyn. Kim tra
        </div>

        <div className="napatm-confirm">
        <button onClick={handleConfirmTransfer}>Xc nhn  chuyn khon</button>

        </div>
      </div>
      <Footer />
    </>
  )
}

export default NapATMPage
